import React from "react";
import { NavLink } from "react-router-dom";
import Styled from "styled-components";

const NavStyled = Styled.nav`
  width: 100%;
  height: 60px;
  background-color: #000;
  color: #fff;

 ul {
   height: 100%;
   list-style:none;
   display: flex;
   gap: 50px;
   align-content: center;
   justify-content: center;
   box-sizing: border-box;
   align-items: center;
 } 

 ul li a{
   text-decoration: none;
   color: #fff;
 }

 .active{
   color: red
 }

`;

function Nav() {
  return (
    <NavStyled>
      <ul>
        <li>
          <NavLink exact activeClassName="active" to="/planets">
            HOME
          </NavLink>
        </li>
        <li>
          <NavLink exact to="/planets/favouritePlanets">
            FAVOURITES
          </NavLink>
        </li>
        <li>
          <NavLink exact to="/planets/about">
            ABOUT
          </NavLink>
        </li>
      </ul>
    </NavStyled>
  );
}

export default Nav;
