import React from "react";
import Styled from "styled-components";

const AboutContainer = Styled.div`
    background-color: #000;
    color: #fff;
    width: 100%;
    display: grid;
    align-items: center;
    max-width: 900px;
    margin: 10% auto!important;
    height: 200px;
    text-align: center;
    padding: 20px;
    font-size: 1.3em;

#author{
    font-size: 2.2em;
    display: block;
    margin: 20px 0px;
}


`;

const About = () => {
  return (
    <AboutContainer>
      <p>
        Created By
        <span id="author">MM Nauman</span>
      </p>
    </AboutContainer>
  );
};

export default About;
