import React, { useEffect, useState } from "react";
import Styled from "styled-components";

const Bookmarked = ({ savedPlanets, RemoveFavourite }) => {
  const SavedContainer = Styled.div`
  max-width: 900px;
  margin: 40px auto!important;
  display: grid;
  grid-template-columns: repeat(3, minmax(100px, 1fr));
  justify-content: center;
  gap: 50px;
  align-items: stretch;
  div{
    box-shadow: 0px 0px 2px 1px #000;
    align-items: center;
    font-size: 1.2em;
    flex-basis: 200px;
    text-align: center;
    padding: 20px;
  }

div button{
  width: 100%;
  margin-top: 20px;
  padding: 10px;
  outline: none!important;
  cursor: pointer;
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
  border: 2px solid;

}

div button:hover{
  background-color: #d86570;
}
  
  .Added i{
    color: red;
  }
  
  `;

  // const [FavPlanets, setFavPlanets] = useState(savedPlanets);

  // useEffect(() => {
  //   const FavItems = JSON.parse(localStorage.getItem("SP")) || FavPlanets;
  //   setFavPlanets(FavItems);
  // }, [setFavPlanets]);

  return (
    <SavedContainer>
      {savedPlanets.map((planet) => {
        return (
          <div key={planet.id} id={planet.id}>
            <h2>{planet.name}</h2>
            <button
              onClick={() => {
                RemoveFavourite(planet);
              }}
            >
              Remove From Favourites
            </button>
          </div>
        );
      })}
    </SavedContainer>
  );
};

export default Bookmarked;
