import Styled from "styled-components";
import React, { useState, useEffect, useRef } from "react";
import SearchBar from "./SearchBar";
import EscapeString from "escape-string-regexp";

const PlanetsStyled = Styled.div`
max-width: 900px;
margin: 40px auto!important;
ul {
  height: 100vh;
  display: grid;
  list-style: none;
  grid-template-columns: repeat(4, minmax(100px, 1fr));
  align-items: center;
  justify-content: center;
  gap: 40px;
}
li{
  box-shadow: 0px 0px 2px 1px #000;
  display: grid;
  justify-content: center;
  height: 150px;
  align-items: center;
  border-radius: 50%;
  font-size: 1.2em;
}

li span{
  text-align: center;
  font-size: 1.4em;
  cursor: pointer;
}

input{
  height: 40px;
  padding-left: 10px;
  width: 100%;
  margin-bottom: 20px;
  font-size: 16px;
}

.Added i{
  color: red;
}

.PlanetsCount{
  font-size: 1.4em;
  text-align: center;
  color: #2e2e2e
}

.resetAll{
  background: none;
  outline: none;
  border: none;
  font-size: 1em;
  color: blue;
  cursor: pointer;
  margin: 0 10px;
}

.resetAll:hover{
  text-decoration: underline;
}

`;

function Planets({ planets, onSave }) {
  const [SavedPlanets, setSavedPlanets] = useState([]);
  const [Query, setQuery] = useState("");

  useEffect(() => {
    const getPlanets = JSON.parse(localStorage.getItem("SP")) || planets;
    setSavedPlanets(getPlanets);
  }, [setSavedPlanets]);

  const searchHandler = (e) => {
    setQuery(e.target.value);
  };

  const ShowAll = () => {
    setQuery("");
  };

  let filteredPlanets = [];
  if (Query) {
    const match = new RegExp(EscapeString(Query), "i");
    filteredPlanets = planets.filter((p) => match.test(p.name));
    console.log(filteredPlanets);
  } else {
    filteredPlanets = planets;
    console.log(filteredPlanets);
  }

  function titleHandler(evnt) {
    if (evnt.target.parentElement.className === "Added") {
      evnt.target.parentElement.title = "Remove From Favourites";
    } else {
      evnt.target.parentElement.title = "Add To Favourites";
    }
  }

  return (
    <PlanetsStyled>
      <SearchBar value={Query} searchHandler={searchHandler} />
      {Query && (
        <p className="PlanetsCount">
          Now Showing {filteredPlanets.length} of {planets.length} Total
          <button onClick={ShowAll} className="resetAll">
            Show All
          </button>
        </p>
      )}
      <ul>
        {filteredPlanets.map((el) => {
          return (
            <li key={el.id}>
              {el.name}
              <span
                className={el.isFavourite === true ? "Added" : "Removed"}
                id={el.id}
                onClick={(e) => {
                  onSave(e, el);
                }}
                onMouseMove={(evnt) => {
                  titleHandler(evnt);
                }}
              >
                <i className="fas fa-heart"></i>
              </span>
            </li>
          );
        })}
      </ul>
    </PlanetsStyled>
  );
}

export default Planets;
