import React, { useEffect, useState } from "react";
import { Route, Switch } from "react-router-dom";
import Styled from "styled-components";
import Bookmarked from "./components/Bookmarked";
import Nav from "./components/Nav";
import Planets from "./components/Planets";
import axios from "axios";
import About from "./components/About";

const MainContainer = Styled.div`
  *{
    margin: 0px;
    padding: 0px;
  }
  body{
    margin: 0px!important;
  }
.Added{
  color: red;
}

`;

function App() {
  const [AllPlanets, setAllPlanets] = useState([]);
  const [FPlanets, setFPlanets] = useState([]);
  const [SearchedText, setSearchedText] = useState("");

  useEffect(() => {
    async function getPlanets() {
      const { data } = await axios.get(
        "https://assignment-machstatz.herokuapp.com/planet"
      );
      console.log(data);
      setAllPlanets(data);
    }
    getPlanets();
  }, [setAllPlanets]);

  const favoriteHandler = (e, el) => {
    console.log(e.target, el);
    el.isFavourite = true;
    if (el.isFavourite === true) {
      e.target.closest("span").className = "Added";
    } else {
      e.target.closest("span").className = "removed";
    }
    const UniquePlanets = FPlanets.includes(el)
      ? FPlanets.filter((i) => {
          el.isFavourite = false;
          return i !== el;
        }) // remove item
      : [...FPlanets, el];

    setFPlanets(UniquePlanets);
    localStorage.setItem("SP", JSON.stringify(UniquePlanets));
  };

  const RemoveFavourite = (planet) => {
    planet.isFavourite = false;
    console.log(planet);
    return setFPlanets((currentState) => {
      return currentState.filter((p) => {
        return planet.id !== p.id;
      });
    });
  };

  return (
    <MainContainer>
      <Nav />
      <Route
        exact
        path="/planets"
        render={(props) => (
          <Planets {...props} onSave={favoriteHandler} planets={AllPlanets} />
        )}
      ></Route>

      <Route
        path="/planets/favouritePlanets"
        render={(props) => (
          <Bookmarked
            RemoveFavourite={RemoveFavourite}
            {...props}
            savedPlanets={FPlanets}
          />
        )}
      ></Route>

      <Route exact path="/planets/about" component={About} />
    </MainContainer>
  );
}

export default App;
